import React, { useEffect } from 'react';
import { Polyline, Popup } from 'react-leaflet';

// Global color assignment tracker (outside component to persist across renders)
const routeColorAssignments = new Map();
let colorIndex = 0;

const RoutePolyline = ({ route, gtfsService }) => {
  const [coordinateSegments, setCoordinateSegments] = React.useState([]);
  const [loading, setLoading] = React.useState(true);

  // Function to get a unique color for a route
  const getRouteColor = (routeId) => {
    // If route already has an assigned color, return it
    if (routeColorAssignments.has(routeId)) {
      return routeColorAssignments.get(routeId);
    }
    
    // Available colors palette
    const colors = [
      '#2196F3', // Blue
      '#E91E63', // Pink
      '#4CAF50', // Green
      '#FF9800', // Orange
      '#9C27B0', // Purple
      '#F44336', // Red
      '#00BCD4', // Cyan
      '#8BC34A', // Light Green
      '#FF5722', // Deep Orange
      '#607D8B', // Blue Grey
      '#795548', // Brown
      '#009688', // Teal
      '#CDDC39', // Lime
      '#FFC107', // Amber
      '#673AB7', // Deep Purple
      '#3F51B5', // Indigo
      '#FF6EC7', // Hot Pink
      '#40E0D0', // Turquoise
      '#FFD700', // Gold
      '#32CD32', // Lime Green
      '#FF1493', // Deep Pink
      '#00FA9A', // Medium Spring Green
      '#1E90FF', // Dodger Blue
      '#FF4500', // Orange Red
      '#9370DB', // Medium Purple
      '#00CED1', // Dark Turquoise
      '#98FB98', // Pale Green
      '#F0E68C', // Khaki
      '#DDA0DD', // Plum
      '#87CEEB', // Sky Blue
      '#DC143C', // Crimson
      '#00FFFF', // Aqua
      '#ADFF2F', // Green Yellow
      '#FF69B4', // Hot Pink
      '#CD853F', // Peru
    ];
    
    // Assign the next available color
    const assignedColor = colors[colorIndex % colors.length];
    routeColorAssignments.set(routeId, assignedColor);
    colorIndex++;
    
    return assignedColor;
  };

  // Function to calculate distance between two coordinates in meters
  const calculateDistance = (coord1, coord2) => {
    const R = 6371000; // Earth's radius in meters
    const lat1Rad = coord1[0] * Math.PI / 180;
    const lat2Rad = coord2[0] * Math.PI / 180;
    const deltaLat = (coord2[0] - coord1[0]) * Math.PI / 180;
    const deltaLng = (coord2[1] - coord1[1]) * Math.PI / 180;

    const a = Math.sin(deltaLat / 2) * Math.sin(deltaLat / 2) +
      Math.cos(lat1Rad) * Math.cos(lat2Rad) *
      Math.sin(deltaLng / 2) * Math.sin(deltaLng / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

    return R * c;
  };

  // Function to split coordinates into segments based on distance threshold
  const splitIntoSegments = (coordinates, maxDistance = 2000) => {
    if (coordinates.length < 2) return [coordinates];
    
    const segments = [];
    let currentSegment = [coordinates[0]];
    
    for (let i = 1; i < coordinates.length; i++) {
      const distance = calculateDistance(coordinates[i - 1], coordinates[i]);
      
      if (distance <= maxDistance) {
        // Continue current segment
        currentSegment.push(coordinates[i]);
      } else {
        // Start new segment
        if (currentSegment.length > 1) {
          segments.push(currentSegment);
        }
        currentSegment = [coordinates[i]];
      }
    }
    
    // Add the last segment if it has more than one point
    if (currentSegment.length > 1) {
      segments.push(currentSegment);
    }
    
    return segments.filter(segment => segment.length > 1);
  };
  
  useEffect(() => {
    const loadRouteCoordinates = async () => {
      try {
        setLoading(true);
        const coords = await gtfsService.getRouteCoordinates(route.routeId);
        
        // Split coordinates into segments to avoid "flying" connections
        const segments = splitIntoSegments(coords);
        setCoordinateSegments(segments);
      } catch (error) {
        console.error(`Error loading coordinates for route ${route.shortName}:`, error);
        setCoordinateSegments([]);
      } finally {
        setLoading(false);
      }
    };

    if (route && gtfsService) {
      loadRouteCoordinates();
    }
  // eslint-disable-next-line
  }, [route, gtfsService]);

  if (loading || coordinateSegments.length === 0) {
    return null;
  }

  return (
    <>
      {coordinateSegments.map((segment, index) => (
        <Polyline
          key={`${route.routeId}-segment-${index}`}
          positions={segment}
          pathOptions={{
            color: route.color || getRouteColor(route.routeId),
            weight: 10,
            opacity: 0.9,
            dashArray: route.type === 'Tram' ? '15, 8' : null
          }}
        >
          {/* Only show popup on the first segment to avoid duplicates */}
          {index === 0 && (
            <Popup>
              <div style={{ textAlign: 'center' }}>
                <strong>{route.shortName}</strong>
                {route.longName && <div>{route.longName}</div>}
                <div style={{ fontSize: '12px', color: '#666', marginTop: '5px' }}>
                  : {route.type}
                </div>
              </div>
            </Popup>
          )}
        </Polyline>
      ))}
    </>
  );
};

export default RoutePolyline;