import Papa from 'papaparse';

class GTFSService {
  constructor() {
    this.routes = [];
    this.stops = [];
    this.shapes = [];
    this.trips = [];
    this.stopTimes = new Map(); // Maps stop_id to array of stop_times
    this.routeIdToShortName = new Map(); // Maps route_id to route_short_name
    this.shortNameToRouteId = new Map(); // Maps route_short_name to route_id
    this.routeShapes = new Map(); // Maps route_id to array of shape_ids
    this.shapesData = new Map(); // Maps shape_id to array of coordinates
    this.isLoaded = false;
    this._loadingPromises = {}; // Track loading promises to prevent duplicate calls
  }

  async loadRoutes() {
    if (this.isLoaded) {
      return this.routes;
    }

    // If already loading, return the existing promise
    if (this._loadingPromises.routes) {
      return this._loadingPromises.routes;
    }

    // Create a single loading promise and cache it
    this._loadingPromises.routes = this._doLoadRoutes();
    return this._loadingPromises.routes;
  }

  async _doLoadRoutes() {
    // Load routes and trips in parallel
    await Promise.all([
      this._loadRoutesData(),
      this._loadTripsData()
    ]);

    this.isLoaded = true;
    console.log(`Loaded ${this.routes.length} routes with shape mappings`);
    return this.routes;
  }

  async _loadRoutesData() {
    try {
      const response = await fetch('/gtfs-static/routes.txt');
      const csvText = await response.text();

      const parsed = Papa.parse(csvText, {
        header: true,
        skipEmptyLines: true,
        transformHeader: (header) => header.trim(),
        transform: (value) => value.trim()
      });

      this.routes = parsed.data
        .filter(route => route.route_short_name && route.route_short_name !== '')
        .map(route => {
          // Build route mapping
          this.routeIdToShortName.set(route.route_id, route.route_short_name);
          this.shortNameToRouteId.set(route.route_short_name, route.route_id);

          return {
            routeId: route.route_id,
            shortName: route.route_short_name,
            longName: route.route_long_name || '',
            description: route.route_desc || '',
            type: this.getRouteTypeName(route.route_type),
            color: route.route_color ? `#${route.route_color}` : '#2196F3',
            textColor: route.route_text_color ? `#${route.route_text_color}` : '#FFFFFF',
            sortOrder: parseInt(route.route_sort_order) || 999
          };
        })
        .sort((a, b) => {
          // Sort by route short name numerically
          const aNum = parseInt(a.shortName);
          const bNum = parseInt(b.shortName);

          if (!isNaN(aNum) && !isNaN(bNum)) {
            return aNum - bNum;
          }

          // If not both numbers, sort alphabetically
          return a.shortName.localeCompare(b.shortName);
        });

      console.log(`Loaded ${this.routes.length} routes data`);
      return this.routes;
    } catch (error) {
      console.error('Error loading GTFS routes:', error);
      return [];
    }
  }

  async _loadTripsData() {
    // Return early if already loaded to prevent duplicate calls
    if (this.tripToRoute && this.tripToRoute.size > 0) {
      return this.routeShapes;
    }

    // If already loading trips, wait for existing promise
    if (this._loadingPromises.trips) {
      return this._loadingPromises.trips;
    }

    // Create promise and cache it
    this._loadingPromises.trips = this._doLoadTripsData();
    return this._loadingPromises.trips;
  }

  async _doLoadTripsData() {
    try {
      const response = await fetch('/gtfs-static/trips.txt');
      const csvText = await response.text();

      const parsed = Papa.parse(csvText, {
        header: true,
        skipEmptyLines: true,
        transformHeader: (header) => header.trim(),
        transform: (value) => value.trim()
      });

      // Initialize trip-to-route mapping if not already done
      if (!this.tripToRoute) {
        this.tripToRoute = new Map();
      }

      // Use temporary Sets for building, then convert to arrays at the end
      const tempRouteShapes = new Map();

      // Group trips by route_id and collect shape_ids, also build trip-route mapping
      parsed.data.forEach(trip => {
        const routeId = trip.route_id;
        const shapeId = trip.shape_id;
        const tripId = trip.trip_id;

        // Build trip-to-route mapping
        if (tripId && routeId) {
          this.tripToRoute.set(tripId, routeId);
        }

        // Build route shapes mapping using temporary Sets
        if (routeId && shapeId) {
          if (!tempRouteShapes.has(routeId)) {
            tempRouteShapes.set(routeId, new Set());
          }
          tempRouteShapes.get(routeId).add(shapeId);
        }
      });

      // Convert Sets to Arrays and store in the instance variable
      tempRouteShapes.forEach((shapeSet, routeId) => {
        this.routeShapes.set(routeId, Array.from(shapeSet));
      });

      console.log(`Loaded trip-route mapping for ${this.tripToRoute.size} trips`);
      return this.routeShapes;
    } catch (error) {
      console.error('Error loading trips:', error);
      return new Map();
    }
  }

  getRouteTypeName(routeType) {
    const types = {
      '0': 'Tram',
      '1': 'Metro',
      '2': 'Rail',
      '3': 'Bus',
      '4': 'Ferry',
      '5': 'Cable',
      '6': 'Gondola',
      '7': 'Funicular'
    };
    return types[routeType] || 'Bus';
  }

  getRoutesByType(type) {
    return this.routes.filter(route => route.type === type);
  }

  getRouteByShortName(shortName) {
    return this.routes.find(route => route.shortName === shortName);
  }

  searchRoutes(query) {
    if (!query || query.length === 0) {
      return this.routes;
    }

    const lowerQuery = query.toLowerCase();
    return this.routes.filter(route =>
      route.shortName.toLowerCase().includes(lowerQuery) ||
      route.longName.toLowerCase().includes(lowerQuery)
    );
  }

  // Helper to get emoji for route type
  getRouteEmoji(type) {
    switch (type) {
      case 'Tram': return '';
      case 'Metro': return '';
      case 'Bus': return '';
      case 'Trolley': return '';
      case 'Ferry': return '';
      default: return '';
    }
  }

  // Helper to determine if route is likely a trolley based on naming patterns
  isLikelyTrolley(route) {
    const shortName = route.shortName.toUpperCase();
    return shortName.startsWith('TB') || shortName.startsWith('T');
  }

  // Helper to determine if route is likely a metro/tram
  isLikelyMetro(route) {
    const shortName = route.shortName.toUpperCase();
    return shortName.startsWith('TM') || route.type === 'Tram' || route.type === 'Metro';
  }

  // Convert route_id to route_short_name
  getRouteShortName(routeId) {
    return this.routeIdToShortName.get(routeId) || routeId;
  }

  // Convert route_short_name to route_id
  getRouteId(shortName) {
    return this.shortNameToRouteId.get(shortName) || shortName;
  }

  // Check if a vehicle belongs to a tracked route
  isVehicleInRoute(vehicle, trackedRoute) {
    if (!vehicle || !trackedRoute) return false;

    // Extract route_id from bus ID if it follows the pattern: "ROUTE_ID-..." (e.g., "TB2-TB613-3-14-14637221171")
    // Only use the first part (up to first dash) to avoid false matches
    let routeIdFromBusId = null;
    if (vehicle.id && typeof vehicle.id === 'string') {
      const idParts = vehicle.id.split('-');
      if (idParts.length > 1) {
        routeIdFromBusId = idParts[0]; // First part is the route_id (e.g., "TB2")
      }
    }

    // PRIORITIZE bus ID extraction matching - this is the most reliable method
    if (routeIdFromBusId) {
      // ONLY EXACT ROUTE ID MATCH - no conversion logic to prevent cross-contamination
      if (routeIdFromBusId === trackedRoute.routeId) {
        console.log(` Bus ${vehicle.id} matched route ${trackedRoute.shortName} (${trackedRoute.routeId}) via exact route_id: ${routeIdFromBusId}`);
        return true;
      }

      // If no exact match, do not try conversion - this prevents cross-route contamination
      //console.log(` Bus ${vehicle.id} with route_id ${routeIdFromBusId} does not match tracked route ${trackedRoute.routeId}`);
    }

    // FALLBACK to other matching strategies only if bus ID extraction fails
    // (these are kept for compatibility with buses that don't follow the ID pattern)

    // ONLY EXACT MATCHES - no conversion to prevent cross-contamination
    if (vehicle.route === trackedRoute.routeId) {
      console.log(` Bus ${vehicle.id} matched route ${trackedRoute.shortName} (${trackedRoute.routeId}) via direct route field match`);
      return true;
    }

    // Very conservative fallback - only if vehicle explicitly has the exact shortName AND routeId
    if (vehicle.routeShortName === trackedRoute.shortName && vehicle.route === trackedRoute.routeId) {
      console.log(` Bus ${vehicle.id} matched route ${trackedRoute.shortName} (${trackedRoute.routeId}) via exact shortName + routeId match`);
      return true;
    }

    // No match found - this prevents cross-route contamination
    return false;
  }

  async loadShapes() {
    if (this.shapesData.size > 0) {
      return this.shapesData;
    }

    // If already loading, return existing promise
    if (this._loadingPromises.shapes) {
      return this._loadingPromises.shapes;
    }

    // Create and cache loading promise
    this._loadingPromises.shapes = this._doLoadShapes();
    return this._loadingPromises.shapes;
  }

  async _doLoadShapes() {
    try {
      const response = await fetch('/gtfs-static/shapes.txt');
      const csvText = await response.text();

      const parsed = Papa.parse(csvText, {
        header: true,
        skipEmptyLines: true,
        transformHeader: (header) => header.trim(),
        transform: (value) => value.trim()
      });

      // Group shapes by shape_id
      const shapeGroups = new Map();

      parsed.data.forEach(row => {
        const shapeId = row.shape_id;
        if (!shapeId) return;

        if (!shapeGroups.has(shapeId)) {
          shapeGroups.set(shapeId, []);
        }

        shapeGroups.get(shapeId).push({
          lat: parseFloat(row.shape_pt_lat),
          lng: parseFloat(row.shape_pt_lon),
          sequence: parseInt(row.shape_pt_sequence) || 0
        });
      });

      // Sort each shape by sequence and store
      shapeGroups.forEach((points, shapeId) => {
        const sortedPoints = points
          .filter(p => !isNaN(p.lat) && !isNaN(p.lng))
          .sort((a, b) => a.sequence - b.sequence)
          .map(p => [p.lat, p.lng]);

        if (sortedPoints.length > 0) {
          this.shapesData.set(shapeId, sortedPoints);
        }
      });

      console.log(`Loaded ${this.shapesData.size} shapes`);
      return this.shapesData;
    } catch (error) {
      console.error('Error loading shapes:', error);
      return new Map();
    }
  }

  async loadStops() {
    if (this.stops.length > 0) {
      return this.stops;
    }

    // If already loading, return existing promise
    if (this._loadingPromises.stops) {
      return this._loadingPromises.stops;
    }

    // Create and cache loading promise
    this._loadingPromises.stops = this._doLoadStops();
    return this._loadingPromises.stops;
  }

  async _doLoadStops() {
    try {
      const response = await fetch('/gtfs-static/stops.txt');
      const csvText = await response.text();

      const parsed = Papa.parse(csvText, {
        header: true,
        skipEmptyLines: true,
        transformHeader: (header) => header.trim(),
        transform: (value) => value.trim()
      });

      this.stops = parsed.data
        .filter(stop => stop.stop_lat && stop.stop_lon && stop.stop_name)
        .map(stop => ({
          id: stop.stop_id,
          name: stop.stop_name,
          lat: parseFloat(stop.stop_lat),
          lng: parseFloat(stop.stop_lon),
          code: stop.stop_code || '',
          description: stop.stop_desc || ''
        }))
        .filter(stop => !isNaN(stop.lat) && !isNaN(stop.lng));

      console.log(`Loaded ${this.stops.length} stops`);
      return this.stops;
    } catch (error) {
      console.error('Error loading stops:', error);
      return [];
    }
  }

  async getRouteShapes(routeId) {
    // Load shapes if not already loaded
    await this.loadShapes();

    if (this.routeShapes.has(routeId)) {
      return this.routeShapes.get(routeId);
    }

    // If we don't have the route shapes cached, we'd need to load trips.txt
    // For now, return empty array
    console.log(`No shapes found for route ${routeId}`);
    return [];
  }

  // Get all coordinate points for a route (for drawing on map)
  async getRouteCoordinates(routeId) {
    const shapeIds = await this.getRouteShapes(routeId);
    const allCoordinates = [];

    shapeIds.forEach(shapeId => {
      const coordinates = this.shapesData.get(shapeId);
      if (coordinates) {
        allCoordinates.push(...coordinates);
      }
    });

    return allCoordinates;
  }

  async loadStopTimes() {
    if (this.stopTimes.size > 0) {
      return this.stopTimes;
    }

    // If already loading, return existing promise
    if (this._loadingPromises.stopTimes) {
      return this._loadingPromises.stopTimes;
    }

    // Create and cache loading promise
    this._loadingPromises.stopTimes = this._doLoadStopTimes();
    return this._loadingPromises.stopTimes;
  }

  async _doLoadStopTimes() {
    try {
      const response = await fetch('/gtfs-static/stop_times.txt');
      const csvText = await response.text();

      const parsed = Papa.parse(csvText, {
        header: true,
        skipEmptyLines: true,
        transformHeader: (header) => header.trim(),
        transform: (value) => value.trim()
      });

      // Group stop times by stop_id
      parsed.data.forEach(row => {
        const stopId = row.stop_id;
        if (!stopId) return;

        if (!this.stopTimes.has(stopId)) {
          this.stopTimes.set(stopId, []);
        }

        this.stopTimes.get(stopId).push({
          tripId: row.trip_id,
          arrivalTime: row.arrival_time,
          departureTime: row.departure_time,
          stopSequence: parseInt(row.stop_sequence) || 0
        });
      });

      // Sort stop times by stop sequence
      this.stopTimes.forEach((times, stopId) => {
        times.sort((a, b) => a.stopSequence - b.stopSequence);
      });

      console.log(`Loaded stop times for ${this.stopTimes.size} stops`);
      return this.stopTimes;
    } catch (error) {
      console.error('Error loading stop times:', error);
      return new Map();
    }
  }

  // Get stop times for a specific stop
  getStopTimes(stopId) {
    return this.stopTimes.get(stopId) || [];
  }

  // Get formatted arrival times for display
  getFormattedStopTimes(stopId, limit = 5) {
    const times = this.getStopTimes(stopId);
    return times.slice(0, limit).map(time => ({
      ...time,
      formattedArrival: this.formatTime(time.arrivalTime),
      formattedDeparture: this.formatTime(time.departureTime)
    }));
  }

  // Get formatted arrival times for a specific route at a stop
  getFormattedStopTimesForRoute(stopId, routeId) {
    if (!this.tripToRoute || !this.stopTimes.has(stopId)) {
      return [];
    }

    const allTimes = this.stopTimes.get(stopId) || [];

    // Filter times to only include those for the specific route
    const routeTimes = allTimes.filter(time => {
      const tripRouteId = this.tripToRoute.get(time.tripId);
      return tripRouteId === routeId;
    });

    // Sort by arrival time
    routeTimes.sort((a, b) => {
      if (!a.arrivalTime || !b.arrivalTime) return 0;
      return a.arrivalTime.localeCompare(b.arrivalTime);
    });

    // More aggressive deduplication - group by arrival time and pick representative entry
    const timeGroups = new Map();

    routeTimes.forEach(time => {
      const arrivalTime = time.arrivalTime;
      if (arrivalTime) {
        if (!timeGroups.has(arrivalTime)) {
          timeGroups.set(arrivalTime, time);
        }
      }
    });

    // Convert back to array and sort
    const uniqueTimes = Array.from(timeGroups.values()).sort((a, b) => {
      if (!a.arrivalTime || !b.arrivalTime) return 0;
      return a.arrivalTime.localeCompare(b.arrivalTime);
    });

    return uniqueTimes.map(time => ({
      ...time,
      formattedArrival: this.formatTime(time.arrivalTime),
      formattedDeparture: this.formatTime(time.departureTime)
    }));
  }

  // Format time from GTFS format (HH:MM:SS) to display format
  formatTime(timeString) {
    if (!timeString) return '';
    const [hours, minutes] = timeString.split(':');
    return `${hours.padStart(2, '0')}:${minutes.padStart(2, '0')}`;
  }

  // Get stops for a specific route
  async getStopsForRoute(routeId) {
    // Ensure we have all necessary data loaded
    await Promise.all([
      this.loadStopTimes(),
      this._loadTripRouteMapping(),
      this.loadStops()
    ]);

    // Get all stop_ids that are served by this route's trips
    const routeStopIds = new Set();

    // Iterate through all stop times and find stops for our route
    this.stopTimes.forEach((times, stopId) => {
      times.forEach(time => {
        // Check if this trip belongs to our route
        if (this.tripToRoute && this.tripToRoute.get(time.tripId) === routeId) {
          routeStopIds.add(stopId);
        }
      });
    });

    // Filter stops to only include those served by this route
    const routeStops = this.stops.filter(stop => routeStopIds.has(stop.id));

    return routeStops;
  }

  async _loadTripRouteMapping() {
    // If already loaded by _loadTripsData, return it
    if (this.tripToRoute) {
      return this.tripToRoute;
    }

    // If routes haven't been loaded yet, load them first (which will populate tripToRoute)
    if (!this.isLoaded) {
      await this.loadRoutes();
      return this.tripToRoute;
    }

    // Fallback: if for some reason tripToRoute is still not available, initialize empty
    if (!this.tripToRoute) {
      this.tripToRoute = new Map();
    }

    return this.tripToRoute;
  }
}

// Export singleton instance
export const gtfsService = new GTFSService();
export default gtfsService;