import axios from 'axios';
import GtfsRealtimeBindings from 'gtfs-realtime-bindings';

const GTFS_BASE_URL = 'https://gtfs.sofiatraffic.bg/api/v1';

// Try different configurations to bypass CORS
const createApiClient = (config) => {
  return axios.create({
    baseURL: GTFS_BASE_URL,
    timeout: 30000,
    ...config
  });
};

// Helper function to parse protobuf data to a more readable format
const parseProtobufToJson = (buffer, gtfsService = null) => {
  try {
    console.log(' Parsing protobuf buffer of size:', buffer.byteLength, 'bytes');

    // Convert ArrayBuffer to Uint8Array for protobuf parser
    const uint8Array = new Uint8Array(buffer);

    // Parse GTFS Realtime feed
    const feed = GtfsRealtimeBindings.transit_realtime.FeedMessage.decode(uint8Array);
    console.log(' Processing', feed.entity.length, 'entities from GTFS feed');

    // Extract vehicle positions
    const vehicles = [];

    feed.entity.forEach((entity, index) => {
      if (entity.vehicle && entity.vehicle.position) {
        const vehicle = entity.vehicle;
        const position = vehicle.position;

        const routeId = vehicle.trip?.route_id || 'Unknown';

        // Use gtfsService to convert route_id to route_short_name if available
        const routeShortName = gtfsService ?
          gtfsService.getRouteShortName(routeId) : routeId;

        // Extract vehicle info with better GTFS integration
        const vehicleInfo = {
          id: entity.id || `vehicle-${index}`,
          vehicleId: vehicle.vehicle?.id || entity.id,
          route: routeId, // Keep original route_id for internal matching
          routeShortName: routeShortName, // Human-readable route number
          tripId: vehicle.trip?.trip_id,
          latitude: position.latitude,
          longitude: position.longitude,
          bearing: position.bearing || 0,
          speed: position.speed ? (position.speed * 3.6) : null, // Convert m/s to km/h
          timestamp: vehicle.timestamp ? new Date(vehicle.timestamp * 1000) : new Date(),
          occupancyStatus: vehicle.occupancy_status,
          currentStatus: vehicle.current_status,
          // Add timestamp for tracking updates
          lastUpdate: new Date().toLocaleTimeString()
        };

        vehicles.push(vehicleInfo);
      }
    });

    console.log(` Successfully parsed ${vehicles.length} vehicles from GTFS Realtime feed`);
    if (vehicles.length > 0) {
      console.log(' Sample vehicles:', vehicles.slice(0, 2).map(v => ({
        id: v.id,
        route: v.route,
        routeShortName: v.routeShortName,
        position: `${v.latitude?.toFixed(4)}, ${v.longitude?.toFixed(4)}`
      })));
    }

    return { vehicles };
  } catch (error) {
    console.error('Error parsing GTFS protobuf:', error);
    console.error('Error details:', error.message);

    // Return empty data structure to maintain compatibility
    return {
      vehicles: []
    };
  }
};

export const sofiaApi = {
  getVehiclePositions: async (gtfsService = null) => {
    console.log(' Attempting to fetch vehicle positions from GTFS Realtime API...');
    
    // Try multiple strategies to bypass CORS
    const strategies = [
      // Strategy 1: Try with minimal headers
      {
        name: 'Minimal headers',
        config: {
          responseType: 'arraybuffer'
        }
      },
      
      // Strategy 2: Try with fetch instead of axios
      {
        name: 'Fetch API',
        useFetch: true
      },
      
      // Strategy 3: Try JSON response instead of protobuf
      {
        name: 'JSON response',
        config: {
          responseType: 'json',
          headers: {
            'Accept': 'application/json'
          }
        }
      },
      
      // Strategy 4: Try with no special headers at all
      {
        name: 'Default request',
        config: {}
      }
    ];

    for (const strategy of strategies) {
      try {
        console.log(` Trying strategy: ${strategy.name}`);
        
        if (strategy.useFetch) {
          // Try with fetch API
          const response = await fetch(`${GTFS_BASE_URL}/vehicle-positions`, {
            method: 'GET',
            mode: 'cors'
          });
          
          if (!response.ok) {
            throw new Error(`HTTP ${response.status}: ${response.statusText}`);
          }
          
          const buffer = await response.arrayBuffer();
          console.log(' Fetch successful, parsing protobuf...');
          return parseProtobufToJson(buffer, gtfsService);
          
        } else {
          // Try with axios
          const api = createApiClient(strategy.config);
          const response = await api.get('/vehicle-positions');
          
          console.log(' Axios successful:');
          console.log('  Status:', response.status);
          console.log('  Data type:', typeof response.data);
          
          if (strategy.config.responseType === 'arraybuffer') {
            console.log('  Data length:', response.data?.byteLength, 'bytes');
            return parseProtobufToJson(response.data, gtfsService);
          } else {
            // Handle JSON response
            console.log('  JSON Data:', response.data);
            return response.data;
          }
        }
        
      } catch (error) {
        console.warn(` Strategy "${strategy.name}" failed:`, error.message);
        
        if (strategy === strategies[strategies.length - 1]) {
          console.error(' All strategies failed, returning empty data');
          return { vehicles: [] };
        }
      }
    }
  },

  // Add a simple method to test basic connectivity
  testConnection: async () => {
    try {
      console.log(' Testing basic connection to Sofia API...');
      
      // Try a simple GET request with minimal configuration
      const response = await fetch(`${GTFS_BASE_URL}/vehicle-positions`, {
        method: 'GET',
        mode: 'no-cors' // This might help bypass CORS for testing
      });
      
      console.log('Test response:', response.status, response.statusText);
      return { success: true, status: response.status };
      
    } catch (error) {
      console.error('Connection test failed:', error);
      return { success: false, error: error.message };
    }
  },

  // For development testing - returns empty data by default
  getMockVehiclePositions: async () => {
    return {
      vehicles: []
    };
  }


};