import React, { useEffect, useState, useMemo } from 'react';
import { Marker, Popup } from 'react-leaflet';
import L from 'leaflet';

// Create custom bus stop icon with route color - memoized
const createBusStopIcon = (() => {
  const iconCache = new Map();
  
  return (routeColor) => {
    const color = routeColor || '#4CAF50';
    
    if (iconCache.has(color)) {
      return iconCache.get(color);
    }
    
    const icon = new L.Icon({
      iconUrl: 'data:image/svg+xml;base64,' + btoa(`
        <svg width="20" height="20" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <circle cx="12" cy="12" r="8" fill="${color}" stroke="white" stroke-width="2"/>
          <circle cx="12" cy="12" r="3" fill="white"/>
        </svg>
      `),
      iconSize: [20, 20], // Smaller icons for better performance
      iconAnchor: [10, 10],
      popupAnchor: [0, -10]
    });
    
    iconCache.set(color, icon);
    return icon;
  };
})();

// Component for lazy-loading arrival times
const ArrivalTimes = ({ stop, route, gtfsService }) => {
  const [times, setTimes] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadTimes = async () => {
      try {
        setLoading(true);
        const arrivalTimes = await gtfsService.getFormattedStopTimesForRoute(stop.id, route.routeId);
        setTimes(arrivalTimes || []);
      } catch (error) {
        console.error('Error loading arrival times:', error);
        setTimes([]);
      } finally {
        setLoading(false);
      }
    };

    loadTimes();
  }, [stop.id, route.routeId, gtfsService]);

  if (loading) {
    return (
      <div style={{ color: '#666', fontSize: '11px', fontStyle: 'italic' }}>
          ...
      </div>
    );
  }

  return (
    <div style={{
      marginTop: '10px',
      padding: '8px',
      backgroundColor: '#f5f5f5',
      borderRadius: '4px',
      fontSize: '12px'
    }}>
      <div style={{
        fontWeight: 'bold',
        marginBottom: '5px',
        color: route.color || '#2196F3',
        fontSize: '13px'
      }}>
         {route.shortName} - :
      </div>
      {times.length > 0 ? (
        <div style={{ maxHeight: '200px', overflowY: 'auto' }}>
          {times.map((time, index) => (
            <div key={index} style={{
              marginBottom: '3px',
              color: '#666',
              padding: '2px 4px',
              borderRadius: '3px',
              backgroundColor: index % 2 === 0 ? '#ffffff' : '#f9f9f9',
              fontSize: '12px'
            }}>
              {time.formattedArrival}
            </div>
          ))}
        </div>
      ) : (
        <div style={{ color: '#888', fontStyle: 'italic', fontSize: '11px' }}>
              
        </div>
      )}
    </div>
  );
};

const BusStopMarkers = ({ route, gtfsService, routeIndex = 0, totalRoutes = 1 }) => {
  const [stops, setStops] = useState([]);
  const [loading, setLoading] = useState(true);

  // Memoize offset calculation
  const offset = useMemo(() => {
    if (totalRoutes === 1) return [0, 0];

    const offsetDistance = 0.0001;
    const angle = (routeIndex * 360) / totalRoutes;
    const radians = (angle * Math.PI) / 180;

    return [
      offsetDistance * Math.cos(radians),
      offsetDistance * Math.sin(radians)
    ];
  }, [routeIndex, totalRoutes]);

  // Memoize icon
  const busStopIcon = useMemo(() => {
    return createBusStopIcon(route.color);
  }, [route.color]);

  useEffect(() => {
    const loadStops = async () => {
      if (!gtfsService || !route?.routeId) {
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        
        // Only load stops for this specific route
        const routeStops = await gtfsService.getStopsForRoute(route.routeId);
        
        // Additional filtering to ensure stops actually belong to this route
        const filteredStops = routeStops.filter(stop => {
          // Verify the stop is actually served by this route
          return gtfsService.isStopServedByRoute && gtfsService.isStopServedByRoute(stop.id, route.routeId);
        });
        
        setStops(filteredStops.length > 0 ? filteredStops : routeStops);
        
      } catch (error) {
        console.error(`Error loading stops for route ${route.shortName}:`, error);
        setStops([]);
      } finally {
        setLoading(false);
      }
    };

    loadStops();
  }, [gtfsService, route?.routeId, route?.shortName]);

  if (loading || stops.length === 0) {
    return null;
  }

  return (
    <>
      {stops.map((stop) => {
        const adjustedPosition = [
          stop.lat + offset[0],
          stop.lng + offset[1]
        ];

        return (
          <Marker
            key={`${stop.id}-${route.routeId}`}
            position={adjustedPosition}
            icon={busStopIcon}
          >
            <Popup maxWidth={300}>
              <div style={{ minWidth: '200px' }}>
                <h4 style={{ margin: '0 0 10px 0', color: '#333' }}>
                  {stop.name}
                </h4>
                {stop.code && (
                  <div style={{ fontSize: '12px', color: '#666', marginBottom: '5px' }}>
                    : {stop.code}
                  </div>
                )}
                {stop.description && (
                  <div style={{ fontSize: '12px', color: '#666', marginBottom: '10px' }}>
                    {stop.description}
                  </div>
                )}
                <div style={{ fontSize: '12px', color: '#888', borderTop: '1px solid #eee', paddingTop: '8px' }}>
                  <div>: {stop.lat.toFixed(6)}</div>
                  <div>: {stop.lng.toFixed(6)}</div>
                </div>
                
                {/* Lazy-loaded arrival times */}
                <ArrivalTimes stop={stop} route={route} gtfsService={gtfsService} />
              </div>
            </Popup>
          </Marker>
        );
      })}
    </>
  );
};

export default BusStopMarkers;